.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;

  &-login {
    &__btn {
      position: relative;
      padding: 0;
      border: none;
      border-radius: 0;
      background-color: transparent;
      border-color: transparent;
      width: 62px;
      height: 62px;

      @include media-breakpoint-down(md) {
        width: 50px;
        height: 50px;
      }

      &:hover,
      &:active,
      &:focus {
        background-color: transparent !important;
        border-color: transparent !important;
        box-shadow: none !important;
      }

      &[aria-expanded="true"] {
        img {
          box-shadow: 0 0 0 4px #424753;
        }
      }

      & > * {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
      }

      &-avatar {
        width: 35px;
        height: 35px;
        border-radius: 100%;
        box-shadow: 0 0 0 4px #fff;
      }

      & + .dropdown-menu {
        margin-top: -5px;
        border: 0 !important;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);
        transition: opacity 0.5s, transform 0.5s;
        transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
        min-width: 175px;

        &.show {
          transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
        }
      }
    }

    .dropdown {
      &-divider {
        margin: 0;
        border-top: 1px solid rgba(0, 0, 0, 0.05);
      }

      &-item {
        color: #222;
        padding: 0.8rem 1.5rem;
        font-weight: bold;
        font-size: 0.8125rem;
        font-weight: 600;
        line-height: 1.1538;
        text-transform: uppercase;
        cursor: pointer;

        span {
          color: inherit;
          font-weight: bold;
          font-size: inherit;
          font-weight: inherit;
          line-height: inherit;
          text-transform: inherit;
        }

        &:active {
          color: white;

          span {
            color: white;
          }

          svg {
            fill: white;
          }
        }
      }

      &-header {
        font-size: 1rem;
        font-weight: 600;
        color: #222;
      }
    }

    .svg-icon--header {
      width: 27px;
      height: 27px;

      @include media-breakpoint-down(md) {
        width: 25px;
        height: 25px;
      }
    }
  }

  &-logout {
    &__svg,
    &__label {
      display: inline-block;
      vertical-align: middle;
    }

    &__svg {
      margin-right: 10px;

      svg {
        fill: #666;
        width: 15px;
        height: 15px;
      }
    }
  }
}
